import React from 'react';
import TableDragSelect from "react-table-drag-select";
import "react-table-drag-select/style.css";

import {
  Button,
 } from 'reactstrap';


export default class SearchTimetable extends React.Component {
  state = {
    timetable_cells: [
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
    ]
  };

  render = () =>
    <div className="SF">
      <TableDragSelect value={this.state.timetable_cells} onChange={this.handleChange}>
        <tr>
          <td disabled />
          <td disabled>一</td>
          <td disabled>二</td>
          <td disabled>三</td>
          <td disabled>四</td>
          <td disabled>五</td>
        </tr>
        <tr>
          <td disabled>9:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />

        </tr>
        <tr>
          <td disabled>10:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />
        </tr>
        <tr>
          <td disabled>11:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />
        </tr>
        <tr>
          <td disabled>12:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />
        </tr>
        <tr>
          <td disabled>13:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />
        </tr>
        <tr>
          <td disabled>14:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />
        </tr>
        <tr>
          <td disabled>15:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />
        </tr>
        <tr>
          <td disabled>16:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />
        </tr>
        <tr>
          <td disabled>17:00</td>
          <td />
          <td />
          <td />
          <td />
          <td />
        </tr>
      </TableDragSelect>
      <Button size='sm' id="search-timetable-btn"outline color="danger" onClick={this.handleClick}>Reset</Button>
    </div>;

  handleChange = timetable_cells => this.setState({ timetable_cells });

  handleClick = () => {
    const timetable_cells = [
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
    ];
    this.setState({ timetable_cells });
    console.log(this.state)
  };
}



