import React, { Component } from 'react';

import Timetable from 'react-timetable-events';
import moment from "moment";
import {
  Collapse0,
  Collapse,
  Input,
  Button,
 } from 'reactstrap';


import { slide as Slide } from 'react-burger-menu'


export default class Timetable2 extends React.Component {

  handleSlide() {
    console.log(this.state.slide);
    this.setState({ slide: !this.state.slide });
  }

  constructor(props){
    super()
    this.state = {
      slide : true,
      events: {
        monday: [
          {
            id: 1,
            name: 'Custom Event 1',
            type: 'custom',
            startTime: moment('2018-02-23T11:30:00'),
            endTime: moment('2018-02-23T13:30:00')
          }
        ],
        tuesday: [
          {
            id: 2,
            name: 'Custom Event 2',
            type: 'custom',
            startTime: moment('2018-02-22T12:30:00'),
            endTime: moment('2018-02-22T14:30:00')
          },

          {
            id: 3,
            name: 'Custom Event 3',
            type: 'custom',
            startTime: moment('2018-02-22T16:30:00'),
            endTime: moment('2018-02-22T18:45:00')
          }
        ],
        wednesday: [],
        thursday: [],
        friday: []
      }
    }
  }
  render () {
    return (
      <div className="Timetable">
        <Slide isOpen={ false }>
          <Timetable events={this.state.events}/>
        </Slide>
        <Button color="primary" onClick={() => this.handleSlide(this)}>Search</Button>
      </div>
    );
  }
}



